.advantages {
  background-color: $color-light-grey;
}

.advantages__container {
  padding: 105px 165px 130px;

  @include vp-1199 {
    padding: 97px 40px 64px;
  }

  @include vp-767 {
    padding: 65px 0;
  }
}

.advantages__container h2 {
  margin-top: 0;
  font-family: $ff-title;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.6px;
  text-align: center;
  line-height: 1;
  margin-bottom: 19px;

  @include vp-1199 {
    font-size: 42px;
    margin-bottom: 20px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 1px;
    margin-bottom: 17px;
  }
}

.advantages__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-template-rows: auto [last-line];
  column-gap: 53px;

  @include vp-1199 {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr auto 1fr;
    row-gap: 41px;
    column-gap: 35px;

    @media screen and (min-width: 840px) and (max-width: 1199px) {
      justify-items: center;
    }
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    grid-template-rows: none;
    row-gap: 11px;
  }
}

.advantages__list-item {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  column-gap: 27px;
  padding-top: 42px;
  padding-bottom: 75px;
  border-bottom: 2px solid $color-advantages-border;

  &:nth-child(-n + 2) {
    padding-top: 0;
  }

  &:nth-last-child(-n + 2) {
    padding-bottom: 0;
    border-bottom: none;
  }

  @include vp-1199 {
    border: none;
    padding-bottom: 0;
    padding-top: 2px;
    column-gap: 28px;
    justify-content: flex-end;

    &:nth-last-child(-n + 2) {
      position: relative;
      bottom: 26px;
    }
  }

  @include vp-767 {
    flex-direction: column-reverse;
    padding-right: 15px;
    padding-left: 15px;

    &:nth-last-child(-n + 2) {
      position: relative;
      bottom: 0;
    }
  }
}

.advantages__list-content {
  padding-top: 45px;

  @include vp-767 {
    padding-top: 0;
  }
}

.advantages__number p {
  font-size: 14px;
  margin: 0;
  color: $color-light-blue;
  position: relative;
  bottom: 8px;
  left: 0;
  display: flex;
  flex-direction: column;
  line-height: 0.9;

  @include vp-767 {
    align-items: center;
    margin-bottom: 12px;
  }
}

.advantages__number span {
  position: relative;
  top: 20px;
  right: 1px;
  line-height: 1.5;
  font-size: 71px;
  font-weight: 700;
  background: $gradient-advantages-number;
  background-clip: text;
  color: $color-transparent;

  @include vp-1199 {
    font-size: 58px;
  }

  @include vp-767 {
    bottom: -13px;
    right: 0;
  }
}

.advantages__list-content p {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.4;
  margin: 0;

  @include vp-767 {
    text-align: center;
  }
}

.advantages__list-item--main {
  grid-column: 2;
  grid-row: 1 / last-line;
  grid-row-start: span 9000;
  max-width: 500px;
  background-color: $color-blue;
  color: $color-default-white;
  padding: 65px 34px 54px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;
  margin-top: 39px;

  @include vp-1199 {
    grid-column: 1 / -1;
    grid-row: 2;
    justify-self: center;
    max-width: 100%;
    min-width: 442px;
    padding: 30px 34px 36px;
    margin-bottom: 0;
    margin-top: 0;
  }

  @include vp-767 {
    min-width: 100%;
    grid-row: 3;
    padding: 30px 70px 43px;
    flex-direction: column;
    margin-top: 28px;
    margin-bottom: 0;
  }
}

.advantages__list-item h3 {
  font-size: 22px;
  line-height: 1;
  font-weight: 700;
  margin: 0;
  position: relative;
  bottom: 10px;
  left: 2px;

  @include vp-1199 {
    font-size: 20px;
    left: 0;
    right: 2px;
    line-height: 0.7;
  }

  @include vp-767 {
    text-align: center;
    line-height: 1.2;
    margin-bottom: 3px;
  }
}

.advantages__main-number {
  flex-grow: 1;

  @include vp-1199 {
    margin-bottom: 13px;
  }

  @include vp-767 {
    margin-bottom: 5px;
  }
}

.advantages__main-number h3 {
  color: $color-default-white;
  text-align: center;
  font-size: 26px;
  line-height: 1;
  padding-right: 5px;
  display: flex;
  flex-direction: column;

  @include vp-1199 {
    font-size: 22px;
    padding-right: 0;
  }

  @include vp-767 {
    margin-bottom: 0;
    padding-top: 0;
  }
}

.advantages__main-number span {
  color: $color-default-white;
  font-size: 70px;
  line-height: 1;
  margin-bottom: 13px;

  @include vp-1199 {
    margin-bottom: 19px;
  }

  @include vp-767 {
    padding-top: 10px;
    margin-bottom: 14px;
  }
}

.advantages__list-item--main p {
  text-align: center;
  line-height: 1.4;
  margin: 0;
  padding-top: 35px;

  @include vp-1199 {
    line-height: 1.5;
    padding-top: 0;
  }
}
